header {
	position: fixed;
	width: 100%;
	top: 0;
	background: linear-gradient(to bottom, rgba(0,0,0,0.3), transparent);
	z-index: 5;
	transition: all 0.3s ease;

	.container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0px 8%;
	}

	&.sticky {
		background: white;
		box-shadow: 0 0 20px rgba(0,0,0,0.05);

		a {
			color: #333;
			transition: border-bottom 0s;

			&:hover,
			&:focus {
				color: #333;
				border-bottom: 3px solid #333;
				padding-bottom: 22px;
			}
		}

		h1 a {
			border: 1px solid #333;
			font-size: 14px;
			padding: 14px;
			transition: all 0.3s ease;

			&:hover,
			&:focus {
				background: #333;
				color: white;
				border-bottom: 1px solid #333;
				padding-bottom: 15px;
			}
		}
	}

	a {
		color: white;
		opacity: 0.8;

		&:hover,
		&:focus {
			opacity: 1;
			color: white;
		}
	}

	h1 {
		position: relative;
		margin: 0;
		line-height: 0;
		z-index: 5;

		a {
			text-transform: uppercase;
			border: 1px solid white;
			display: inline-block;
			font-size: 18px;
			letter-spacing: 3px;
			padding: 20px;

			&:hover,
			&:focus {
				background: white;
				color: #333;
			}
		}
	}
}

ul.menu {
	margin: 0;
	padding: 0;
	li {
		display: inline-block;
		margin-right: 10px;
		padding: 0;

		a {
			display: block;
			padding: 25px 10px;
			font-family: $fontTitle;
			text-transform: uppercase;
			font-size: 14px;
			font-weight: 600;
			letter-spacing: 1px;
		}
	}
}

.mobileMenu {
	display: none;
	position: relative;
	height: 25px;
	width: 35px;
	z-index: 5;

	span {
		position: absolute;
		width: 100%;
		height: 2px;
		background: white;
		transition: all 0.3s ease;

		&:nth-child(1) {
			top: 0;
		}

		&:nth-child(2) {
			top: 11px;
		}

		&:nth-child(3) {
			bottom: 0;
		}
	}

	&.show {
		span {
			background: #333;

			&:nth-child(1) {
				transform: rotate(45deg);
				top: 10px;
			}

			&:nth-child(2) {
				width: 0;
			}

			&:nth-child(3) {
				transform: rotate(-45deg);
				top: 10px;
			}
		}
	}
}

@media screen and (max-width: 750px) {
	header.mobile {
		.container{
			padding: 20px 8%;
		}
		.mobileMenu {
			display: block;
		}

		h1 a {
			font-size: 14px;
			padding: 15px;
		}

		&.show h1 a {
			color: #333;
			border: 1px solid #333;
		}

		nav {
			position: absolute;
			opacity: 0;
			visibility: hidden;
			height: 100vh;
			width: 100%;
			left: 0;
			top: 0;
			background: rgba(255,255,255,0.8);
			transition: all 0.3s ease;

			&.show {
				opacity: 1;
				visibility: visible;
			}
		}

		ul.menu {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			text-align: center;
			height: 100vh;

			li {
				margin: 0;
				padding: 2% 0;

				a {
					color: #333;
					font-size: 20px;
					letter-spacing: 5px;
					padding: 10px 0;
				}
			}
		}

		&.sticky {
			h1 a {
				font-size: 14px;
				padding: 15px;
			}

			.mobileMenu {
				border-bottom: none;

				span {
					background: #333;
				}
			}

			&.show {
				background: transparent;
			}
		}
	}
}